import React from 'react'
import {Card,Button,Radio} from 'antd'

export default class Buttons extends React.Component{
  state = {
    loading: false,
    iconLoading: false,
    size:'default'
  };
  enterLoading = () => {
    this.setState({ loading: true });
  };
  closeLoading = () => {
    this.setState({ loading: false });
  };

  enterIconLoading = () => {
    this.setState({ iconLoading: true });
  };
  handleSizeChange = e => {
    this.setState({ size: e.target.value });
  };

  render(){
    const { size } = this.state;
    return(
      <div className='buttons'>
      <Card size="small" title="基础按钮" style={{marginBottom:'10px'}}>
        <p>
          <Button type="primary" style={{size:'20px'}}>Imooc</Button>
          <Button>Imooc</Button>
          <Button type="dashed">Imooc</Button>
          <Button type="danger">Imooc</Button>
          <Button disabled>Imooc</Button>
        </p>
      </Card>
      <Card size="small" title="图形按钮" style={{marginBottom:'10px'}} >
        <p>
          <Button icon="plus">创建</Button>
          <Button icon="edit" >编辑</Button>
          <Button type="danger" icon="delete" >删除</Button>
          <Button type="primary" shape="circle" icon="search" ></Button>
          <Button type="primary" icon="search" >搜索</Button>
          <Button type="primary" icon="download" >下载</Button>
        </p>
      </Card>
      <Card size="small" title="Loading按钮" style={{marginBottom:'10px'}}>
        <p>
          <Button type="primary" loading>确定</Button>
          <Button type="primary" shape="circle" loading={this.state.loading} ></Button>
          <Button loading={this.state.loading} onClick={this.enterLoading}>点击加载</Button>
          <Button shape="circle" loading={this.state.loading} onClick={this.enterLoading}></Button>
          <Button onClick={this.closeLoading} >关闭</Button>
        </p>
      </Card>
      <Card size="small" title="按钮组" style={{marginBottom:'10px'}}>
        <p>
          <Button type="primary" icon="left">返回</Button>
          <Button type="primary" icon="right">前进</Button>
        </p>
      </Card>
      <Card size="small" title="按钮尺寸" >
        <p>
          <Radio.Group onChange={this.handleSizeChange} >
            <Radio value="small" size={size}>小</Radio>
            <Radio value="default" size={size}>中</Radio>
            <Radio value="large" size={size}>大</Radio>
          </Radio.Group>
          <Button type="primary" size={size}>Imooc</Button>
          <Button size={size}>Imooc</Button>
          <Button size={size}>Imooc</Button>
          <Button type="danger" size={size}>Imooc</Button>
        </p>

      </Card>
      </div>
    )
  }
}
