import React,{Component} from 'react'
import {Row,Col} from 'antd'


import './header.less'
import Utils from '../../utils/utils'
export default class Header extends Component{
  state={weather:''}

    UNSAFE_componentWillMount(){
      //this.setState(this.getWeather('九江'))
      this.setState({username:'天涯一剑'})
      setInterval(()=>{
        const sysTime=Utils.formatDate(new Date().getTime())
        this.setState({sysTime})
      },1000)

    }

  render(){
    return (
      <div className='header'>
        <Row className='header-top'>
          <Col span='24'>
            <span className='login-name'>欢迎：{this.state.username}</span>
            <a href="http://www.baidu.com">退出</a>
          </Col>
        </Row>
        <Row className='header-weather'>
          <Col span='4' className='header-index'>首页</Col>
          <Col span='20' className='weather'>
            <span className='date'>{this.state.sysTime}</span>
            <span className='weather-dtail'>晴</span>
          </Col>
        </Row>
      </div>
    )
  }
}
