import React from 'react'
import {Card,Tabs,message} from 'antd'
import Icon from '@ant-design/icons'
export default class Tab extends React.Component{
  constructor(props) {
      super(props);
      this.newTabIndex = 0;
      const panes = [
        { title: 'Tab 1', content: 'Content of Tab 1', key: '1' },
        { title: 'Tab 2', content: 'Content of Tab 2', key: '2' },
        { title: 'Tab 3', content: 'Content of Tab 3', key: '3', closable: false,},
      ];
      this.state = {
        activeKey: panes[0].key,
        panes,
      };
    }

    onChange = activeKey => {
      this.setState({ activeKey });
    };

    onEdit = (targetKey, action) => {
      this[action](targetKey);
    };

    add = () => {
      const { panes } = this.state;
      const activeKey = `newTab${this.newTabIndex++}`;
      panes.push({ title: 'New Tab', content: 'Content of new Tab', key: activeKey });
      this.setState({ panes, activeKey });
    };

    remove = targetKey => {
      let { activeKey } = this.state;
      let lastIndex;
      this.state.panes.forEach((pane, i) => {
        if (pane.key === targetKey) {
          lastIndex = i - 1;
        }
      });
      const panes = this.state.panes.filter(pane => pane.key !== targetKey);
      if (panes.length && activeKey === targetKey) {
        if (lastIndex >= 0) {
          activeKey = panes[lastIndex].key;
        } else {
          activeKey = panes[0].key;
        }
      }
      this.setState({ panes, activeKey });
    };

  openMess=(key)=>{
     message.success(`Hi,你点击了标签${key}`);
  }

  render(){
    const { TabPane } = Tabs;
    return(
      <div>
        <Card title="Tab标签页">
          <Tabs defaultActiveKey="1" onChange={this.openMess}>
            <TabPane tab="Tab 1" key="1" >欢迎学习React课程</TabPane>
            <TabPane tab="Tab 2" key="2" disabled >欢迎学习React课程</TabPane>
            <TabPane tab="Tab 3" key="3" >React是一门非常受欢迎的MV*框架</TabPane>
          </Tabs>
        </Card><Card title="带图标的Tab标签页">
          <Tabs defaultActiveKey="1" onChange={this.openMess}>
            <TabPane tab={<span><Icon type="delete" />Tab 2</span>} key="1" >删除它就这么简单</TabPane>
            <TabPane tab={<span><Icon type="edit" />Tab 2</span>} key="2" >尝试使用React修改</TabPane>
            <TabPane tab={<span><Icon type="plus" />Tab 2</span>} key="3" >创建属于你的项目</TabPane>
          </Tabs>
        </Card>

        <Card title="可关团Tab标签页">
        <Tabs
          onChange={this.onChange}
          activeKey={this.state.activeKey}
          type="editable-card"
          onEdit={this.onEdit}
        >
          {this.state.panes.map(pane => (
            <TabPane tab={pane.title} key={pane.key} closable={pane.closable}>
            {pane.content}
            </TabPane>
          ))}
      </Tabs>
        </Card>
      </div>
    )
  }
}
