import React from 'react'
import {Card,Button,notification} from 'antd'
export default class Notification extends React.Component{
  openNotification = (type,placement) => {
  notification[type]({
    message:"发工资了",
    description:
      '上月考勤22天， 迟到12天，实发工资250，请笑纳。',
      placement,
    onClick: () => {
      console.log('Notification Clicked!');
    },
  });
};
  render(){
    return(
      <div className='notifica'>
        <Card title="通知提醒框" className="card-wrap">
          <Button type="primary" onClick={() => this.openNotification('success','bottomright')}>Success</Button>
          <Button type="primary" onClick={() => this.openNotification('info','bottomright')}>Info</Button>
          <Button type="primary" onClick={() => this.openNotification('warning','bottomright')}>Warning</Button>
          <Button type="primary" onClick={() => this.openNotification('error','bottomright')}>Error</Button>
        </Card>
        <Card title="通知提醒框方向">
          <Button type="primary" onClick={() => this.openNotification('success','topLeft')}>Success-TopLelt</Button>
          <Button type="primary" onClick={() => this.openNotification('info','topRight')}>Info-TopRight</Button>
          <Button type="primary" onClick={() => this.openNotification('warning','bottomLeft')}>Warning-BottomLeft</Button>
          <Button type="primary" onClick={() => this.openNotification('error','bottomRight')}>Error-Bottomright</Button>
        </Card>


      </div>
    )
  }
}
