import React from 'react'
import {Card,Button,Modal} from 'antd'

export default class Modals extends React.Component{
  state={
    visible1: false,
    visible2: false,
    visible3: false,
    visible4: false,
    visible:false
  }
  handleShow=(type)=>{
    this.setState({
      [type]:true
    })
  }
  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  showModal=(type)=>{
    Modal[type]({
      title:'确认？',
      content:'你确定学会了React了吗？',
      onOK:()=>{console.log('ok')},
      onCancel:()=>{console.log('ok')}
    })
  }
  render(){
    return(
      <div className='register'>
        <Card title='基础模态框' className="card-wrap" >
          <Button type="primary" onClick={()=>this.handleShow("visible1")}>open</Button>
          <Button type="primary" onClick={()=>this.handleShow("visible2")}>自定义页脚</Button>
          <Button type="primary" onClick={()=>this.handleShow("visible3")}>顶部20px弹框</Button>
          <Button type="primary" onClick={()=>this.handleShow("visible4")} >水平垂直剧中</Button>
        </Card>
        <Card title='信息确认框' className="card-wrap" >
          <Button type="primary" onClick={()=>this.showModal("confirm")}>Confirm</Button>
          <Button type="primary" onClick={()=>this.showModal("info")}>Info</Button>
          <Button type="primary" onClick={()=>this.showModal("success")}>Success</Button>
          <Button type="primary" onClick={()=>this.showModal("error")}>Error</Button>
          <Button type="primary" onClick={()=>this.showModal("warning")}>Warning</Button>
        </Card>
        <Modal
          title="React"
          visible={this.state.visible1}
          onOk={this.handleOk}
          onCancel={()=>this.setState({visible1:false})}
        >
          <p>欢迎学习React高级课程</p>
        </Modal>
        <Modal
          title="React"
          visible={this.state.visible2}
          onOk={this.handleOk}
          okText='好的'
          cancelText="算了"
          onCancel={()=>this.setState({visible2:false})}
        >
          <p>欢迎学习React高级课程</p>
        </Modal>
        <Modal
          title="React"
          visible={this.state.visible3}
          onOk={this.handleOk}
          onCancel={()=>this.setState({visible3:false})}
          style={{top:20}}
        >
          <p>欢迎学习React高级课程</p>
        </Modal>
        <Modal
          title="React"
          visible={this.state.visible4}
          onOk={this.handleOk}
          onCancel={()=>this.setState({visible4:false})}
          wrapClassName="vertical-cen"
        >
          <p>欢迎学习React高级课程</p>
        </Modal>
      </div>
    )
  }
}
