import React,{Component} from 'react'
import {Row,Col} from 'antd'
import {Route,Switch,Redirect} from 'react-router-dom'
import  'antd/dist/antd.css'
import NavLeft from './components/nav-left/nav-left'
import Header from './components/header/header'
import Footer from './components/footer/footer'
import './style/home.less'
import HomePage from './pages/homepage'
import Buttons from './pages/home/buttons'
import Modals from './pages/home/modals'
import Loading from './pages/home/loading'
import Notification from './pages/home/notification'
import Message from './pages/home/message'
import Tab from './pages/home/tab'
import Gallery from './pages/home/gallery'
import Carousel from './pages/home/carousel'
export default class Admin extends Component{
  render(){
    return (
      <Row className="container">
        <Col span='3' className="nav-left">
          <NavLeft/>
        </Col>
        <Col span='21' className="main">
          <Header/>
          <Row className="content">
            <Switch>
              <Route path='/admin/homepage' component={HomePage}/>
              <Route path='/admin/ui/buttons' component={Buttons}/>
              <Route path='/admin/ui/modals' component={Modals}/>
              <Route path='/admin/ui/loading' component={Loading}/>
              <Route path='/admin/ui/notification' component={Notification}/>
              <Route path='/admin/ui/message' component={Message}/>
              <Route path='/admin/ui/tab' component={Tab}/>
              <Route path='/admin/ui/gallery' component={Gallery}/>
              <Route path='/admin/ui/carousel' component={Carousel}/>

              <Redirect to='/admin/homepage' />
            </Switch>
            admin
          </Row>
          <Footer/>
        </Col>
      </Row>
    )
  }
}
