import React from 'react'
import {Card,Spin,Alert} from 'antd'
import Icon from '@ant-design/icons';
export default class Loading extends React.Component{

  render(){
    const antIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;
    return(
      <div className='register'>
      <Card size="small" title="spin用法" style={{marginBottom:'10px'}} className="card-wrap">
        <p>
        <Spin size="small" />
        <Spin style={{marginLeft:10,marginRight:10}} />
        <Spin size="large" />
        <Spin indicator={antIcon} style={{marginLeft:10,marginRight:10}} />
        <Spin indicator={antIcon} />
        </p>
      </Card>
      <Card size="small" title="内容遮罩" style={{marginBottom:'10px'}} className="card-wrap">
        <div>
          <Alert message="React" description="欢迎来到React高级实战" type="warning" />
          <Spin ><Alert  message="React" description="欢迎来到React高级实战" /></Spin>
          <Spin tip="加载中..." ><Alert  message="React" description="欢迎来到React高级实战" /></Spin>
          <Spin indicator={antIcon} >
         <Alert  message="React" description="欢迎来到React高级实战" /></Spin>
        </div>
      </Card>
      </div>
    )
  }
}
