import React from 'react'
import {Card,Button,message} from 'antd'
export default class Message extends React.Component{
  openMess=(type)=>{
     message[type](`${type}：恭喜您React课晋级成功。`);
  }
  render(){
    return(
      <div className='register'>
      <Card title="通知提醒框" className="card-wrap">
        <Button type="primary" onClick={() => this.openMess('success')}>Success</Button>
        <Button type="primary" onClick={() => this.openMess('info')}>Info</Button>
        <Button type="primary" onClick={() => this.openMess('warning')}>Warning</Button>
        <Button type="primary" onClick={() => this.openMess('error')}>Error</Button>
      </Card>
      </div>
    )
  }
}
